import React, { useEffect, useState } from "react";
import { Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useAuth } from "../../contexts/AuthContext";
import firebase, { firestore } from "../../firebase";
import { setOrders } from "../../redux/category-reducer";
import { Preloader } from "../preloader/Preloader";
import style from "./Shop.module.scss";

const ProdDetals = (props) => {
  const [loading, setLoading] = useState(true);
  const [product, setProduct] = useState();
  const [bought, setBought] = useState();
  const dispatch = useDispatch();
  const [coment, setComent] = useState("");

  const { currentUser } = useAuth();
  const { orders } = useSelector(({ category }) => {
    return {
      orders: category.orders,
    };
  });
  const productRef = firestore.collection("product").doc(props.match.params.id);

  useEffect(() => {
    productRef
      // .get()
      // .then((doc) => {
      //   if (doc.exists) {
      //     console.log("Document data:", doc.data());
      //     setProduct(doc.data());
      //     setLoading(false);
      //   } else {
      //     console.log("No such document!");
      //   }
      // })
      // .catch((error) => {
      //   console.log("Error getting document:", error);
      // });
      .onSnapshot((doc) => {
        if (doc.exists) {
          console.log("Document data:", doc.data());
          setProduct(doc.data());
          setLoading(false);
        } else {
          console.log("No such document!");
        }
      });
  }, []);

  useEffect(() => {
    if (orders.find((el) => el.id === props.match.params.id)) setBought(true);
    if (!orders.find((el) => el.id === props.match.params.id)) setBought(false);
  }, [orders]);

  const buy = (e) => {
    const item = { ...product, id: e.target.name, quantity: 1 };
    if (!localStorage.getItem("session_json")) {
      localStorage.setItem(
        "session_json",
        JSON.stringify({ purchases: [item] })
      );
      dispatch(setOrders([item]));
    } else {
      let arr = JSON.parse(localStorage.getItem("session_json")).purchases;
      if (arr.length >= 0) {
        if (!arr.find((el) => el.id == e.target.name)) {
          arr.push(item);
        }
      }
      localStorage.setItem("session_json", JSON.stringify({ purchases: arr }));
      dispatch(setOrders(arr));
    }
  };

  const addComent = () => {
    productRef.get().then((doc) => {
      if (doc.data().comments) {
        productRef.update({
          comments: firebase.firestore.FieldValue.arrayUnion({
            date: new Date(),
            description: coment,
            name: currentUser?.displayName || "Anonim",
          }),
        });
      } else {
        productRef.update({
          comments: [
            {
              date: new Date(),
              description: coment,
              name: currentUser?.displayName || "Anonim",
            },
          ],
        });
      }
    });
    setComent("");
  };

  return (
    <div>
      {loading ? (
        <Preloader />
      ) : (
        <div>
          <div className={style.prodDetals}>
            <img src={product.productImage ? product.productImage : null} />
            <div className={style.prodDescription}>
              <p>Бренд: {product.brendName}</p>
              <p>Тип: {product.type}</p>
              <p>Матеріал рами: {product.frameMaterial}</p>
              <p>Ціна: {product.price}</p>
              <Link className="btn btn-info" to={`/shop`}>
                Назад
              </Link>
              {bought ? (
                <input
                  className="float-right btn btn-success"
                  name={props.match.params.id}
                  type="button"
                  value="В корзине"
                  disabled
                />
              ) : (
                <input
                  className="float-right btn btn-success"
                  name={props.match.params.id}
                  type="button"
                  value="Купити"
                  onClick={buy}
                />
              )}
            </div>
          </div>
          {product.comments && (
            <>
              <h5>Coments:</h5>
              {product.comments.map((p) => {
                console.log(p);
                return (
                  <div key={p.date}>
                    <b>{p.name}</b>
                    <p>{p.description}</p>
                  </div>
                );
              })}
            </>
          )}

          <h5>Add coments:</h5>
          <textarea
            onChange={(e) => {
              setComent(e.target.value);
            }}
            value={coment}
          />
          <br />
          <Button variant="primary" onClick={addComent}>
            Add coment
          </Button>
        </div>
      )}
    </div>
  );
};

export default ProdDetals;
