import React from "react";
import style from "./Order.module.scss";
import { Form, Col, Button, Table } from "react-bootstrap";
import { useSelector } from "react-redux";
import MaskedInput from "react-text-mask";
import { useAuth } from "../../contexts/AuthContext";

const Order = (props) => {
  const [totalSum, setTotalSum] = React.useState(0);
  const [totalQuantity, setTotalQuantity] = React.useState(0);
  const { currentUser } = useAuth();
  const [name, setName] = React.useState(currentUser?.displayName);
  const [email, setEmail] = React.useState(currentUser?.email);

  const { orders } = useSelector(({ category }) => {
    return {
      orders: category.orders,
    };
  });

  React.useEffect(() => {
    let quantity = 0;
    let sum = 0;
    for (let i = 0; i < orders.length; i++) {
      quantity += orders[i].quantity;
      sum += orders[i].quantity * orders[i].price;
    }
    setTotalQuantity(quantity);
    setTotalSum(sum);
  });
  const submit = (e) => {
    e.preventDefault();
    for (let i = 0; i < e.target.elements.length - 1; i++) {
      let el = e.target.elements[i];
      console.log(el.name, el.value);
    }
    console.log(orders);
    e.target.reset();

  };

  return (
    <div className={style.container}>
      <h2> Оформлення замовлення </h2>
      <Form className={style.contactInfo} onSubmit={submit}>
        <div className={style.orderContent}>
          <Form.Group controlId="formGridName">
            <Form.Label> Ім'я та Прізвище </Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter name and second name"
              name="name"
              required
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </Form.Group>

          <Form.Group controlId="formGridAddress">
            <Form.Label>Адреса</Form.Label>
            <Form.Control
              placeholder="Вулиця буд./кв."
              name="address"
              required
            />
          </Form.Group>

          <Form.Group controlId="formGridEmail">
            <Form.Label>Email</Form.Label>
            <Form.Control
              placeholder="Email"
              name="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </Form.Group>

          <Form.Row>
            <Form.Group as={Col} controlId="formGridCity">
              <Form.Label>City</Form.Label>
              <Form.Control placeholder="Lviv" name="city" required />
            </Form.Group>
            <Form.Group as={Col} controlId="formGridZip">
              <Form.Label>Zip</Form.Label>
              <Form.Control name="zip" required />
            </Form.Group>
          </Form.Row>
          <Form.Group controlId="formGridPhone">
            <Form.Label>Phone</Form.Label>
            <MaskedInput
              mask={[
                "(",
                /[0-9]/,
                /\d/,
                /\d/,
                ")",
                " ",
                /\d/,
                /\d/,
                /\d/,
                "-",
                /\d/,
                /\d/,
                /\d/,
                /\d/,
              ]}
              className="form-control"
              placeholder="Enter a phone number"
              guide={false}
              name="phone"
              required
            />
          </Form.Group>
          <h2> Деталі замовлення </h2>
          <Table striped bordered hover variant="dark">
            <thead>
              <tr>
                <th>#</th>
                <th>Brend</th>
                <th>Quantity</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {orders.map((el, index) => {
                return (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{el.brendName}</td>
                    <td>{el.quantity}</td>
                    <td>{el.quantity * el.price}</td>
                  </tr>
                );
              })}
            </tbody>
          </Table>
        </div>

        <div className={style.orderConfirmContainer}>
          <div className={style.orderConfirm}>
            <h3> Загалом: </h3>
            <p>
              {totalQuantity} товари на суму {totalSum} ₴
            </p>
            <p>
              {" "}
              До сплати <b> {totalSum} ₴</b>{" "}
            </p>

            <Button variant="primary" type="submit">
              Підтвердити
            </Button>
          </div>
        </div>
      </Form>
    </div>
  );
};
export default Order;
